import React, {useState} from "react";
// import { useFormValidation } from './CustomHooks/useFormValidation';
// import { validateAuth } from './CustomHooks/ValidateAuthentication';
// import { SeccionTitle} from '../SeccionTitle/SeccionTitle';


// MUI Stuff
import { SeccionTitle } from '../SeccionTitle/SeccionTitle'
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button';
import CircularProgress from '@material-ui/core/CircularProgress';

export function Contact(props) {

const initialState = {
  Name: '',
  Case: '',
  email: '',
  message: '',
}

const [values, setValues] = useState(initialState);
const [loading, setLoading] = useState(false);

const handleChange = (e) => {
  setValues({
    ...values,
    [e.target.name]: e.target.value
  })
}

const handleSubmit = (e) => {
  e.preventDefault(); 
  
}
    return (
      <div className="main-contact-container">

          <div className="form-container">
          <SeccionTitle title="Envíame un mensaje" margin= "4vh" textcolor="var(--white2)"/>
                <form onSubmit={handleSubmit}>

                    <TextField
                    id="name"
                    name="name"
                    type="text"
                    label="Nombre"
                    value={values.name}
                    onChange={handleChange}
                    fullWidth
                    />

                    <TextField
                    id="email"
                    name="email"
                    type="email"
                    label="Correo"
                    value={values.email}
                    onChange={handleChange}
                    fullWidth
                    />
                    
                    <TextField
                    id="email"
                    name="case"
                    type="text"
                    label="Asunto"
                    value={values.email}
                    onChange={handleChange}
                    fullWidth
                    />

                    <TextField
                    id="message"
                    name="message"
                    type="text"
                    label="Mesaje"
                    value={values.password}
                    onChange={handleChange}
                    fullWidth
                    multiline
                    rows="8"
                    />

                    <Button
                    disabled={loading}
                    type="submit"
                    variant="contained"
                    color="primary"
                    style={{margin: '25px auto 25px auto'}}
                    >
                        Enviar Mensaje!
                    </Button>
                </form>
          </div>


          <div>
              <SeccionTitle title="Contacto" margin= "4vh" textcolor="var(--white2)"/> 

              <div className="info-contact-container"> 

                <h4> Si necesitas ayuda en algún proyecto, no dudes en ponerte < br /> en contacto conmigo </h4>

                <div className="phone-number">
                    <i className="fas fa-phone-alt"></i>
                    <h4> (+54) 2664871368 </h4>            
                </div>

                <div className="phone-number">
                    <i className="fas fa-envelope"></i>
                    <h4> axelleonr12@gmail.com </h4>            
                </div>

                <div className="contacticons-container"> 

                <a href="https://www.linkedin.com/in/axelleonr/"> <i className="fab fa-linkedin"></i>        </a>
                <a href="https://github.com/AxelLr"> <i className="fab fa-github"></i>          </a>
                <a href="https://www.facebook.com/axel.leon.52"> <i className="fab fa-facebook-square"></i> </a>

                </div>
                    
              </div>

          </div>
          
      </div>
    );
}


    